opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}100%{transform:translateY(50px);opacity:0;-webkit-clip-path:polygon(100% 0, 100% 0%, 0 100%, 0 100%);clip-path:polygon(100% 0, 100% 0%, 0 100%, 0 100%)}}@keyframes titleAnimation1{0%{transform:translateY(-50px);opacity:0;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%)}20%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}80%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}100%{transform:translateY(50px);opacity:0;-webkit-clip-path:polygon(100% 0, 100% 0%, 0 100%, 0 100%);clip-path:polygon(100% 0, 100% 0%, 0 100%, 0 100%)}}@-webkit-keyframes titleAnimation2{0%{transform:translateY(-50px);opacity:0;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%)}20%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}100%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}}@keyframes titleAnimation2{0%{transform:translateY(-50px);opacity:0;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%)}20%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}100%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}}

.container{width:100%;height:100vh;background:#F0E5DE;display:flex;justify-content:center;align-items:center}.container .box{width:250px;height:250px;position:relative;display:flex;justify-content:center;flex-direction:column}.container .box .title{width:100%;position:relative;display:flex;align-items:center;height:50px}.container .box .title .block{width:0%;height:inherit;background:#ABD0CE;position:absolute;-webkit-animation:mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;animation:mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;display:flex}.container .box .title h1{font-family:'Poppins';color:#534847;font-size:32px;-webkit-animation:mainFadeIn 2s forwards;animation:mainFadeIn 2s forwards;-webkit-animation-delay:1.6s;animation-delay:1.6s;opacity:0;display:flex;align-items:baseline;position:relative}.container .box .title h1 span{width:0px;height:0px;border-radius:50%;background:#9B8281;-webkit-animation:load 0.6s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;animation:popIn 0.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;-webkit-animation-delay:2s;animation-delay:2s;margin-left:5px;margin-top:-10px;position:absolute;bottom:13px;right:-12px}.container .box .role{width:100%;position:relative;display:flex;align-items:center;height:30px;margin-top:-10px}.container .box .role .block{width:0%;height:inherit;background:#FADAD8;position:absolute;-webkit-animation:secBlock 1.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;animation:secBlock 1.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;-webkit-animation-delay:2s;animation-delay:2s;display:flex}.container .box .role p{-webkit-animation:secFadeIn 2s forwards;animation:secFadeIn 2s forwards;-webkit-animation-delay:3.1s;animation-delay:3.1s;opacity:0;font-weight:800;font-family:'Lato';color:#534847;font-size:12px;text-transform:uppercase;letter-spacing:5px}@-webkit-keyframes mainBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@keyframes mainBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@-webkit-keyframes secBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@keyframes secBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@-webkit-keyframes mainFadeIn{0%{opacity:0}100%{opacity:1}}@keyframes mainFadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes popIn{0%{width:0px;height:0px;background:#ABD0CE;border:0px solid #ddd;opacity:0}50%{width:10px;height:10px;background:#ABD0CE;opacity:1;bottom:45px}65%{width:7px;height:7px;bottom:0px;width:15px}80%{width:10px;height:10px;bottom:20px}100%{width:7px;height:7px;background:#ABD0CE;border:0px solid #222;bottom:13px}}@keyframes popIn{0%{width:0px;height:0px;background:#ABD0CE;border:0px solid #ddd;opacity:0}50%{width:10px;height:10px;background:#ABD0CE;opacity:1;bottom:45px}65%{width:7px;height:7px;bottom:0px;width:15px}80%{width:10px;height:10px;bottom:20px}100%{width:7px;height:7px;background:#ABD0CE;border:0px solid #222;bottom:13px}}@-webkit-keyframes secFadeIn{0%{opacity:0}100%{opacity:1}}@keyframes secFadeIn{0%{opacity:0}100%{opacity:1}}

.about{position:relative;max-width:44em;margin:0 auto;padding:120px 1em 60px}.about::before{position:fixed;content:"";top:0;width:100%;height:64px;background-color:#fff}.about section{padding:1em;border:1px solid;-webkit-animation:show 2s linear;animation:show 2s linear}.about section .img-wrapper{width:100%}.about section .img-wrapper img{width:100%;object-fit:cover}.about__text{padding:30px 15px 0;text-align:center}.about__text p{font-family:"NanumSquare", sans-serif;line-height:1.8;word-break:keep-all}.about__text p mark{padding:0;background-color:#FADAD8;border-radius:3px}@-webkit-keyframes show{0%{opacity:0}100%{opacity:1}}@keyframes show{0%{opacity:0}100%{opacity:1}}

.navigation{visibility:hidden;-webkit-animation:showNavbar 0s 5s forwards;animation:showNavbar 0s 5s forwards}.navigation .bar{position:fixed;display:flex;justify-content:space-between;align-items:center;z-index:999;width:100%}.navigation .bar .name{margin-left:20px;font-family:"Nanum Myeongjo", serif;text-transform:uppercase;font-weight:700;line-height:4;letter-spacing:3px;color:#5e5e5e}.navigation .bar .name a{text-decoration:none;color:#000}.navigation .bar .hambuger{display:flex;flex-direction:column;justify-content:space-between;width:30px;height:30px;margin-right:20px;cursor:pointer;transition:transform 330ms ease-out}.navigation .bar .hambuger.open{transform:rotate(-45deg)}.navigation .bar .hambuger.open .hambuger__line.start{transform:rotate(-90deg) translateX(3px)}.navigation .bar .hambuger.open .hambuger__line.end{transform:rotate(-90deg) translateX(-3px)}.navigation .bar .hambuger__line{background-color:#000;border-radius:3px;width:100%;height:3px}.navigation .bar .hambuger__line.half{width:50%}.navigation .bar .hambuger__line.start{transition:transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);transform-origin:right}.navigation .bar .hambuger__line.end{align-self:flex-end;transition:transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);transform-origin:left}@-webkit-keyframes showNavbar{to{visibility:visible}}@keyframes showNavbar{to{visibility:visible}}.menu{padding:5% 0;text-align:center;z-index:555;opacity:0;visibility:hidden;transition:all 0.5s}.menu.fixed{position:fixed;width:100%}.menu.active{margin-top:70px;opacity:1;visibility:visible;transition:all 0.5s;z-index:9999}.menu__list{display:inline-block;padding:0}.menu__list li{display:block;margin:50px 0;padding:10px}.menu__list__item{display:inline-block;vertical-align:middle;transform:perspective(1px) translateZ(0);box-shadow:0 0 1px rgba(0,0,0,0);position:relative;overflow:hidden}.menu__list__item::before{content:"";position:absolute;z-index:-1;left:0;right:100%;bottom:0;background:#c49d64;height:4px;transition-property:right;transition-duration:0.3s;transition-timing-function:ease-out}.menu__list__item:hover::before,.menu__list__item:focus::before,.menu__list__item:active::before{right:0}.menu__list__item a{font-family:"Nanum Myeongjo", serif;color:#000;display:block;font-size:32px;font-weight:600;letter-spacing:5px;text-transform:uppercase;text-decoration:none;line-height:0.7}.menu__list__item a:hover{color:#c5c5c5}.backdrop{background:rgba(255,255,255,0.95);height:100%;letter-spacing:0;position:fixed;top:0;width:100%;z-index:555;visibility:hidden;transition:all 0.3s}.backdrop.active{opacity:1;visibility:visible;transition:all 0.5s}

